.mission-statement {
    display: flex;
    flex-direction: column;
    padding: calc(3 * (var(--gap))) var(--gap);
    max-width: calc(var(--width) + 4 * var(--gap));
    margin: 0 auto;

    &__image-container {
        display: flex;
        justify-content: center;
    }

    &__single-image-container {
        flex-shrink: 1;
        max-width: calc(var(--width) / 5);

        &:not(:first-child) {
            margin-left: calc(var(--gap) / 2);
        }

        &:not(:last-child) {
            margin-right: calc(var(--gap) / 2);
        }
    }

    &__content {
        padding: 0 var(--gap);
    }

    &__link {
        margin-bottom: calc(2 * var(--gap));
    }
}
