.top-content {
    &__container {
        margin: 0 auto;
    }

    &__search-input {
        margin-bottom: 0;
    }

    &__list {
        list-style-image: url('/assets/images/arrow_right-blue.svg');
        list-style-position: outside;
        background-color: var(--white);
        padding-top: 1px;
        padding-bottom: 1px;
        font-family: var(--font-heading);
        text-align: left;
    }

    &__list-item {
        padding-left: var(--gap);
    }

    &__link {
        position: relative;
        bottom: 0.2em;
        text-decoration: none;

        &:hover {
            text-decoration: underline;
        }
    }
}

.search-suggestions {
    text-align: left;
    position: relative;
    clear: both;

    &__loading-bar {
        position: absolute;
        width: 100%;
        top: 0;
        min-height: 0.2em;
        background: linear-gradient( 90deg, var(--blue-lighter) 25%, var(--blue) 50%, var(--blue) 75%, var(--blue-lighter) 75% );
        background-size: 400% 400%;
        animation: Loading 6s linear infinite;
        cursor: progress;
    }

    &__panel {
        position: absolute;
        z-index: 1;
        width: 100%;
        background-color: var(--white);
    }

    &__list {
        margin: 0;
        padding: 0;
    }

    &__item {
        list-style: none;
        margin: 0;
        padding: 0.7em 1rem;
    }

    &__link {
        text-decoration: none;
    }

    &__title {
        padding: 1em 1rem;
        font-size: small;
    }

    &__button {
        margin-bottom: 0;
    }
}

@keyframes Loading {
    0% {
        background-position: 100% 50%;
    }

    100% {
        background-position: 0% 50%;
    }
}