.site-header {
    margin-top: -1px;
    padding-left: var(--gap);

    &__container {
        display: flex;
        justify-content: space-between;
    }

    &__logo {
        width: 140px;
    }
    
    &__main-nav,
    &__secondary {
        display: none;
    }

    &__buttons {
        display: flex;


        > * {
            display: flex;
            justify-content: center;
            flex-direction: column;
            background-color: var(--blue);
            color: var(--blue-lightest);
            height: 80px;
            width: 70px;
        }

        img {
            width: 2em;
        }
    }
}