// 800px+
.my-membership {
    &__container {
        display: flex;
        flex-direction: row-reverse;
        align-items: center;

        > * {
            flex-basis: 50%;
        }
    }

    &__form {
        padding-left: var(--gap);
    }

    &__image {
        padding-right: var(--gap);
    }

    &__content {
        flex-direction: row;
        justify-content: space-around;
        
        > * {
            flex-basis: 33.33%;
        }
    }
}