  .search {
    display: flex;

    &__input {
        border-right: none;
        width: calc(100% - 2.8em);
        height: 2.8em;
    }

    &__btn {
        display: flex;
        align-items: center;
        justify-content: center;
        border: none;
        height: 2.8em;
        width: 2.8em;
        background-color: var(--blue);
    }

    &__btn-image {
        width: 80%;
    }
}