// 800px +
.call-to-action {
    display: flex;

    > * {
        flex-basis: 50%;

        &:not(:first-child) {
            margin-left: calc(var(--gap));
        }

        &:not(:last-child) {
            margin-right: calc(var(--gap));
        }
    }

    &__content {
        display: flex;
        flex-direction: column;
        justify-content: center;
        padding-right: var(--gap);
    }
}
