* {
    box-sizing: border-box;
    scroll-margin-top: 20vh;
}

html {
    scroll-behavior: smooth;
}

body {
    font-family: var(--font-body);
    line-height: 1.5;
    color: var(--grey-dark);
    border-top: 5px solid var(--blue);
}

h1, h2, h3, h4, h5 {
    font-family: var(--font-heading);
    color: var(--blue-dark);
}

h1 {
    font-size: 2em;
}

h2 {
    font-size: 1.5rem;
    scroll-margin-top: 25vh;
}

h3 {
    font-size: 1.25rem;
}

h4 {
    font-size: 1rem;
}

p,
li,
blockquote {
    margin: 1em 0;
}

hr {
    border-bottom: none;
    border-top: 2px solid var(--blue);
}

a {
    color: var(--blue);
}

img {
    max-width: 100%;
    height: auto;
}

input {
    width: 100%;
    margin-bottom: var(--gap);
    padding: 0.5rem 1em;
    border: var(--blue-lighter) 2px solid;
    border-radius: 0;
        
    &:focus {
        outline: none;
        border-color: var(--blue);
    }
}

button {
    border-radius: 0;
}

form {
    text-align: left;
}

label {
    display: block;
    width: max-content;
    font-size: small;
}