// 1000px +
.site-header {
    padding: calc(2*(var(--gap))) calc(2*(var(--gap))) 0;

    &__container {
        flex-wrap: wrap;
        align-items: flex-start;
        max-width: var(--width);
        margin-left: auto;
        margin-right: auto;
    }

    &__logo {
        width: 255px;
    }

    &__secondary {
        display: block;
    }

    &__secondary-nav {
        width: 28em;
        display: flex;
        justify-content: space-between;
        align-items: baseline;
        margin-bottom: var(--gap);
    }

    &__main-nav {
        flex-basis: 100%;
        display: flex;
        justify-content: space-between;
        margin-top: var(--gap);
        margin-bottom: calc(0.5*var(--gap));
    }

    &__buttons {
        display: none;
    }

    a {
        text-decoration: none;

        &:hover {
            text-decoration: underline;
        }
    }
}