// 800px +
.mission-statement {
    flex-direction: row;
    justify-content: space-between;
    padding: calc(3*(var(--gap))) calc(2*(var(--gap)));

    &__image-container {
        flex-direction: column;
    }

    &__single-image-container {
        
        &:not(:first-child) {
            margin-left: 0;
            margin-top: 0.5rem;
        }

        &:not(:last-child) {
            margin-right: 0;
            margin-bottom: 0.5rem;
        }
    }

    &__content {
        max-width: 49%;
    }
}
