//
// Fonts
// =========================================

/**
 * Headings
 */
 @font-face {
    font-family: 'Montserrat-Bold';
    src: url('./assets/fonts/Montserrat-Bold.ttf');
}

/**
 * Body - normal
 */
@font-face {
    font-family: 'PublicSans';
    src: url('./assets/fonts/PublicSans-Regular.ttf');
    font-weight: normal;
    font-style: normal;
}

/**
 * Body - italic
 */
@font-face {
    font-family: 'PublicSans';
    src: url('./assets/fonts/PublicSans-Italic.ttf');
    font-weight: normal;
    font-style: italic;
}

/**
 * Body - bold
 */
@font-face {
    font-family: 'PublicSans';
    src: url('./assets/fonts/PublicSans-Bold.ttf');
    font-weight: bold;
    font-style: normal;
}

/**
 * Body - bold italic
 */
@font-face {
    font-family: 'PublicSans';
    src: url('./assets/fonts/PublicSans-BoldItalic.ttf');
    font-weight: bold;
    font-style: italic;
}