// 600px +
.latest-news {
    &__items {
        display: flex;
    }

    &__article {
        flex-basis: 50%;

        &:not(:first-child) {
            margin-left: calc(var(--gap));
        }

        &:not(:last-child) {
            margin-right: calc(var(--gap));
        }
    }
}
