.btn {
    font-family: var(--font-heading);
    background-color: var(--blue);
    color: var(--white);
    text-decoration: none;
    padding: 0.4rem 1em;
    display: inline-block;
    border: 0;
    line-height: 1.5;

    &.is-wide {
        padding-left: calc(2*var(--gap));
        padding-right: calc(2*var(--gap));
    }
}
