.action-banner {
    background-color: var(--blue-light);
    color: var(--white);

    &__images {
        position: relative;
        padding-bottom: 66.67%;

        > * {
            position: absolute;
        }
    }

    &__image {
        display: block;
        width: 100vw;
    }

    &__tabs-bar {
        display: none;
    }

    &__accordion-btn {
        font-family: var(--font-heading);
        color: var(--white);
        background-color: var(--blue);
        border: none;
        border-bottom: var(--blue-light) 1px solid;
        padding: 0.5rem 1.5rem;
        width: 100%;
        text-align: left;

        &:not(.is-active):after {
            float: right;
            content: '\00002B';
            font-family: var(--font-body);
            font-size: large;
        }
    }

    &__accordion-content {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        justify-content: center;
        height: 100%;
        padding-left: 1.5rem;
        padding-right: 1.5rem;
        padding-bottom: 2rem;
    }

    &__heading {
        margin-bottom: 0;
    }

    &__link {
        color: var(--white);
        text-decoration: none;

        &:hover {
            text-decoration: underline;
        }
    }

    &__text {
        min-height: 4.5em;
    }
}
