.site-footer {
    background-color: var(--blue-light);
    color: var(--blue-lightest);

    &__list {
        padding-left: 0;
        margin-bottom: 1em;
        list-style-type: none;
    }

    &__link {
        margin-top: 0;
    }

    h3 {
        color: var(--blue-lightest);
    }

    a {
        color: var(--white);
    }
}