.section {
    padding: calc(2*(var(--gap))) var(--gap);

    &__header {
        margin-bottom: calc(2*(var(--gap)))
    }

    &__body {
        max-width: var(--width);
        margin-left: auto;
        margin-right: auto;
    }

    &__footer {
        display: block;
        margin-top: calc(2*(var(--gap)));
        text-decoration: none;

        &:hover {
            text-decoration: underline;
        }

        &::after {
            content: " ";
            position: relative;
            top: 2px;
            left: 2px;
            display: inline-block;
            height: 1em;
            width: 1em;
            background-image: url('/assets/images/arrow_right-blue.svg');
            background-size: contain;
        }
    }
}
