// 600px +
.action-banner {
    &__container {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;

        > * {
            flex-basis: 50%;
        }
    }

    &__tabs-bar {
        flex-basis: 100%;
        display: flex;
        justify-content: center;
        background-color: var(--blue);
    }

    &__tab {
        border-top: 4px solid transparent;
        transition: border-color 0.6s;

        &.is-active {
            border-color: var(--blue-lightest);
        }

        &:focus,
        &:hover:not([disabled]) {
            outline: none;
            border-color: var(--blue-lighter);
            cursor: pointer;
        }
    }

    &__accordion-btn {
        display: none;
    }
}