.video-viewer {
  &__video-link {
    position: relative;
    display: block;
    line-height: 0;

    &::after {
      content: "\0025B8";
      position: absolute;
      display: block;
      bottom: 0;
      height: 80px;
      width: 80px;
      text-align: center;
      color: var(--white);
      background: var(--blue-dark);
      font-weight: bold;
      opacity: 0.8;
      font-size: 5em;
      line-height: 1;
    }

    &:focus {
      outline: 0;
    }

    &:hover::after,
    &:focus::after {
      color: var(--white);
      background: var(--red);
    }
  }

  &__backdrop {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 2;
    display: grid;
    background: var(--blue-dark-seethrough);
    text-align: center;
    cursor: pointer;
  }

  &__video {
    margin: auto;

    iframe {
      width: 88vw;
      max-width: 160vmin;
      height: 49.5vw;
      max-height: 90vmin;
    }
  }

  &__embedded-video {
    max-width: 100%;
  }

  &__close {
    position: absolute;
    top: 1em;
    right: 1em;
    color: white;
    font-size: xx-large;
    text-decoration: none;
    cursor: pointer;
  }
}
