.whats-on {
    position: relative;

    &__scroll {
        display: flex;
        overflow: auto;
        margin-right: calc(-1*var(--gap));
        margin-left: calc(-1*var(--gap));
        -ms-overflow-style: none;  /* IE and Edge */
        scrollbar-width: none;  /* Firefox */

        &::-webkit-scrollbar {
            display: none;
        }
    }
    
    &__scroll-left,
    &__scroll-right {
        position: absolute;
        top: calc(50% - 1em);
        padding: 1em 1em 0.5em;
        cursor: pointer;
    }

    &__scroll-left {
        left: calc(2px + 2*var(--gap));
    }

    &__scroll-right {
        right: calc(2*var(--gap));
    }

    &__scroll-left-arrow {
        transform: rotate(180deg);
    }

    &__event {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        background-color: var(--white);
        margin-right: var(--gap);
        margin-left: var(--gap);

        &:first-of-type {
            position: relative;
            z-index: 1;
        }
    }

    &__link-card {
        position: relative;
        z-index: 1;
        margin-left: var(--gap);
    }

    &__link-card-link {
        display: flex;
        flex-direction: column;
        justify-content: center;
        height: 100%;
        min-width: 20vw;
        background-color: var(--blue-light);
        color: var(--white);
    }

    &__event-link {
        text-decoration: none;
        line-height: 0;
        width: 12em;
        min-width: 20vw;
    }

    &__text {
        padding: 0 var(--gap);
        margin-top: 0;
    }
}
