.latest-news {
    &__article {
        background-color: var(--white);
        padding-bottom: var(--gap);
        margin-bottom: var(--gap);
    }

    &__image {
        &:hover {
            opacity: 0.9;
        }
    }

    &__article-heading {
        padding-left: var(--gap);
        padding-right: var(--gap);
    }

    &__article-link {
        text-decoration: none;

        &:hover {
            text-decoration: underline;
        }
    }

    &__text {
        padding-left: var(--gap);
        padding-right: var(--gap);
    }
}
