.site-footer {
    &__container {
        display: flex;
    }
    
    &__menu {
        flex-basis: 60%;
    }

    &__list {
        padding-left: 0;
        margin-bottom: 1em;
        list-style-type: none;
        columns: 3;
    }

    &__link {
        margin-top: 0;
    }

    h3 {
        color: var(--blue-lightest);
    }

    a {
        color: var(--white);
    }
}