.call-to-action {
    &__content {
        padding-bottom: var(--gap);
    }

    &__heading {
        margin-bottom: 0;
    }

    &__link {
        text-decoration: none;

        &:hover {
            text-decoration: underline;
        }
    }

    &__buttons {
        > :first-child {
            margin-right: var(--gap);
        }
    }

    &__image {
        width: 100%;
    }
}
