.contact-us {
    &__list {
        color: var(--blue);
        list-style: none;
        margin-bottom: 2rem;
        padding-left: 0;
    }

    &__link {
        text-decoration: none;

        &:hover {
            text-decoration: underline;
        }
    }

    &__image-container {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
    }

    &__single-image-container {
        flex-shrink: 1;
        width: calc(50% - 0.5rem);
        margin: 0.5rem;
        
        &:nth-child(odd) {
            margin-left: 0;
        }

        &:nth-child(even) {
            margin-right: 0;
        }
    }

    &__image {
        height: 100%;
    }
}
