/* Layout */
.is-centred {
    text-align: center;
    align-items: center;
}

.is-fullwidth {
    display: block;
    width: 100%;
}

/* Colour */
.is-blue {
    background-color: var(--blue);
    color: var(--white);
}

.is-blue-lightest {
    background-color: var(--blue-lightest);
}

.is-red {
    background-color: var(--red);
    color: var(--white);
}
