:root {
  /* Sizes */
  --width: 1140px;
  --gap: 1.25rem;

  /* Fonts */
  --font-body: "PublicSans", arial, helvetica, sans-serif;
  --font-heading: "Montserrat-Bold", arial, helvetica, sans-serif;

  /* Colours - primary */
  --blue-dark: #002144;
  --blue: #004288; // RCN logo colour
  --blue-light: #3368a0;
  --blue-lighter: #7fa0c3;
  --blue-lightest: #e5ecf3;

  --red: #da0042; // RCN logo colour

  /* Colours - secondary */
  --brand-secondary-1: #e82e8b;
  --brand-secondary-2: #8bc043;
  --brand-secondary-3: #f47d29;
  --brand-secondary-4: #7c6eb0;
  --brand-secondary-5: #00aeef;

  /* Colours - grey */
  --grey-dark: #39363c;
  --grey: #726b78;
  --grey-light: #d8d8d8;
  --grey-lighter: #e6e6e6;
  --grey-lightest: #f2f2f2;
  --white: #fff;

  /* Colours - seethrough */
  --blue-dark-seethrough: #002144ee;
}
