.mag-feature {
    &__article {
        padding-bottom: var(--gap);
        background-color: var(--blue-light);
        color: var(--white);
    }

    &__article-link {
        text-decoration: none;
        color: var(--white);
    }

    &__tag {
        color: var(--white);
        background-color: var(--blue);
        padding: 0.5rem;
    }

    &__date {
        color: var(--white);
        background-color: var(--blue);
        padding: 0.5rem;
    }

    &__text {
        padding-left: var(--gap);
        padding-right: var(--gap);
    }

    &__image {
        width: 100%;
    }
}
